<template>
    <div class="loginBox">
        <img src="../../assets/login/erha.jpg" alt="" class="logo">
        <p class="name f26 c333 mt-40 text-center">图南智慧校园</p>
        <div  class="loginIn f26 cfff text-center" @click="faceRecognition">刷脸登录</div>
        <router-link class="changeIn f26 text-center"  to="passWordLogin">切换登录方式</router-link>
    </div>
</template>

<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
        msg: 'Welcome to Your Vue.js App'
        }
    },
    methods : {
        faceLoginIn(){
            alert('刷脸')
        },
        otherLoginIn(){
            alert('other')
        },
        faceRecognition() {
            if(!localStorage.getItem('userId')){
                this.$toast('尚未录入人脸信息，请用其他方式登陆');
                return
            }
            var that = this;
            Cordova.exec(that.successFunction, that.failFunction, "Plugin", "faceRecognition", ['2',localStorage.getItem('userId').toString(),'2','1']);//第一个参数 type 1录入2识别 第二个参数 id , 第三个参数 1学生 2老师 3家长 ，1登录识别，第四个参数 是否是登录 1登录2不是登录
        },
        successFunction(result){
            this.$toast('刷脸登录成功');
            localStorage.setItem('zhxy_user',JSON.stringify(result.data));
            this.$router.push({path:'/indexs'});
        },
        failFunction(msg){
            this.$toast('刷脸登录失败');
            // this.$router.push({path:'/login'});
        },
        successCallback(msg){
            if(msg == 0){
                this.$router.push({path:'/indexs'})
            }else{
                alert('失败');
            }
        },
        getRegistrationId(){//获取手机id
            var that = this;
            Cordova.exec(that.success, that.fail, "SmsPlugin", "getRegistrationId", []);
        },
        success(result){
            localStorage.setItem('registrationId',result);
        },
        fail(result){
            this.$toast(result);
        }
    },
    created() {
        this.getRegistrationId();
    },
}
</script>

<style scoped>
    .loginBox{overflow: hidden;height: 100vh;}
    .logo{width: 2rem;height:2rem;display: block;margin: 2.14rem auto 0; border-radius: 50%;}
    .loginIn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to right, #82cbff , #7b7ceb);margin: 1.6rem auto 0;line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff; }
    .changeIn{color: #7b7ceb;height: 0.9rem;width: 6.9rem;border-radius: 0.9rem;border: 2px solid #7b7ceb;line-height: 0.9rem;margin: 0.45rem auto 0;}
</style>



